import React from "react";
import Layout from "../components/layout";
import Seo from "../components/seo";
import BreadCrumb from "../components/common/breadcrumb";
import CryptoMLMIndex from "../screens/crypto-mlm-software-development/index";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { graphql } from "gatsby";

const CryptoMLMPage = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Crypto MLM Software Development Company", link: "" },
  ];
  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <main>
        <CryptoMLMIndex db={db} />
      </main>
    </Layout>
  );
};
export const Head = () => (
  <Seo
    title="Crypto MLM Software Development Company | Justtry Technologies"
    headTitle="Cryptocurrency MLM Software Development Company | Justtry Technologies"
    description=" We are a leading cryptocurrency mlm software development company in India, USA and UK. Our smart contract based mlm software helps you build bitcoin mlm software with dApp smart contract."
    keywords="Cryptocurrency MLM Software, Blockchain MLM Software, Bitcoin MLM Software, Cryptocurrency MLM Software Development, Cryptocurrency MLM Software Development Company, Blockchain Based Cryptocurrency MLM Software Development Company"
  ></Seo>
);

export default CryptoMLMPage;

export const queryData = graphql`
  query MyQuery {
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
